.stakinginfo {
    .stakinginfo-card {
        margin-bottom: 15px;
        h3 {
            margin-bottom: 10px;
            font-weight: bold;
            font-size: 20px;
        }
        background: #ffffff;
        border: 1px solid #e5e9ed;
        box-sizing: border-box;
        border-radius: 8px;
        padding: 30px 20px;
        .card-item {
            font-size: 14px;
            padding: 15px 0px;
            border-top: 1px solid #e5e9ed;
        }
        ul li:nth-child(1) {
            font-weight: normal;
            font-size: 14px;
            display: flex;
            align-self: center;
        }
        ul li:nth-child(2) {
            font-weight: bold;
            font-size: 14px;
        }
        .claim-btn{
            width: 105px;
            font-size: 16px;
        }
    }
    .stakinginfo-card-claim {
        border: 1px solid #d3b3ed;
    }
    .modal-content {
        .close {
            float: right;
            font-size: 1.5rem;
            font-weight: 700;
            line-height: 1;
            color: #fff;
            text-shadow: 0 1px 0 #fff;
            opacity: 1.5;
        }

        .pop-content {
            .lower-avaiable {
                border-top: 1px solid #e5e9ed;
            }
            .btn-div {
                .btn-common {
                    width: 131px;
                    height: 50px;
                    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
                    font-weight: 500;
                    font-size: 15px;
                }
                .btn-common-white {
                    width: 131px;
                    height: 50px;
                    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
                    font-weight: 500;
                    font-size: 15px;
                }
            }
            .avail-now {
                border-top: 1px solid #e5e9ed;
            }
            .bluegrey li:nth-child(2n + 2) {
                font-weight: bold;
                font-size: 18px;
            }
            .bluegrey li:nth-child(2n + 1) {
                font-weight: normal;
                font-size: 16px;
            }
            .tbold {
                font-weight: bold;
                font-size: 20px;
            }
        }
        .btn-common {
            width: 161px;
            height: 60px;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            font-weight: 500;
            font-size: 15px;
        }
        .btn-common-white {
            width: 161px;
            height: 60px;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            font-weight: 500;
            font-size: 15px;
        }
        .select-opt {
            background: #ffffff;
            border: 1px solid #d3b3ed;
            box-sizing: border-box;
            border-radius: 10px;
            padding: 10px;
            font-weight: 500;
            font-size: 20px;
            line-height: 23px;
            outline: none;
            color: #b3b3b3;
           
            option {
                margin-top: 10px;
                font-weight: 500;
                font-size: 20px;
                line-height: 23px;
                padding: 110px;
                color: #475569;
                &:hover{
                   background: #F0E6F9 !important;
                }
            
            }
        }
        
    }
}
